"use client";

import { Photo } from "@/lib/types";
import { useState } from "react";

interface PhotoCardProps {
  photo: Photo;
  index: number;
  onDelete: (id: string) => void;
}

export default function PhotoCard({ photo, index, onDelete }: PhotoCardProps) {
  const [imageLoaded, setImageLoaded] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [expanded, setExpanded] = useState(false);

  // Alternate slight rotations for scrapbook feel
  const rotations = [-1.5, 1, -0.5, 1.5, -1, 0.5];
  const rotation = rotations[index % rotations.length];

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr + "T00:00:00");
    return date.toLocaleDateString("en-US", {
      month: "long",
      day: "numeric",
      year: "numeric",
    });
  };

  return (
    <>
      <div
        className="photo-card animate-fadeInUp opacity-0 relative"
        style={{
          animationDelay: `${index * 0.1}s`,
          animationFillMode: "forwards",
          transform: `rotate(${rotation}deg)`,
        }}
        onMouseEnter={() => setShowDelete(true)}
        onMouseLeave={() => setShowDelete(false)}
      >
        <div className="bg-white rounded-2xl shadow-lg overflow-hidden max-w-sm mx-auto">
          {/* Decorative tape */}
          <div className="relative">
            <div className="absolute top-0 left-1/2 -translate-x-1/2 -translate-y-1/2 w-16 h-5 bg-amber-200/70 rounded-sm z-20 rotate-[-2deg]" />
          </div>

          {/* Image */}
          <div
            className="relative cursor-pointer overflow-hidden"
            onClick={() => setExpanded(true)}
          >
            {!imageLoaded && (
              <div className="w-full aspect-[4/3] skeleton" />
            )}
            <img
              src={photo.url}
              alt={photo.caption || "Memory"}
              className={`w-full aspect-[4/3] object-cover transition-all duration-500 hover:scale-105 ${
                imageLoaded ? "opacity-100" : "opacity-0 absolute"
              }`}
              onLoad={() => setImageLoaded(true)}
            />
          </div>

          {/* Caption & Date */}
          <div className="p-4 pt-3">
            <div className="flex items-center gap-1.5 mb-1.5">
              <span className="text-rose-400 text-xs">&#x2665;</span>
              <time className="text-xs text-rose-400 font-medium tracking-wide uppercase">
                {formatDate(photo.date)}
              </time>
            </div>
            {photo.caption && (
              <p className="text-stone-700 text-sm leading-relaxed font-light">
                {photo.caption}
              </p>
            )}
          </div>

          {/* Delete button */}
          <button
            onClick={(e) => {
              e.stopPropagation();
              if (confirm("Remove this memory?")) {
                onDelete(photo.id);
              }
            }}
            className={`absolute top-3 right-3 w-7 h-7 rounded-full bg-white/80 backdrop-blur-sm flex items-center justify-center text-rose-400 hover:text-rose-600 hover:bg-white transition-all duration-200 shadow-sm z-20 ${
              showDelete ? "opacity-100 scale-100" : "opacity-0 scale-75"
            }`}
            aria-label="Delete photo"
          >
            <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
      </div>

      {/* Expanded/lightbox view */}
      {expanded && (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/70 backdrop-blur-sm animate-fadeIn"
          onClick={() => setExpanded(false)}
        >
          <div
            className="relative max-w-3xl max-h-[90vh] animate-scaleIn"
            onClick={(e) => e.stopPropagation()}
          >
            <img
              src={photo.url}
              alt={photo.caption || "Memory"}
              className="max-w-full max-h-[80vh] object-contain rounded-lg shadow-2xl"
            />
            {(photo.caption || photo.date) && (
              <div className="absolute bottom-0 left-0 right-0 p-4 bg-gradient-to-t from-black/60 to-transparent rounded-b-lg">
                <time className="text-white/80 text-xs font-medium tracking-wide uppercase">
                  {formatDate(photo.date)}
                </time>
                {photo.caption && (
                  <p className="text-white text-sm mt-1">{photo.caption}</p>
                )}
              </div>
            )}
            <button
              onClick={() => setExpanded(false)}
              className="absolute -top-3 -right-3 w-8 h-8 rounded-full bg-white shadow-lg flex items-center justify-center text-stone-500 hover:text-stone-700 transition-colors"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        </div>
      )}
    </>
  );
}
